<template>
  <div id="new-restaurant">
    <h3>New Restaurant</h3>
    <div class="form-wrapper">
      <form @submit.prevent="saveRestaurant" class="form">
        <label>Name: </label>
        <input type="text" v-model="name" required>

        <label>Location: </label>
        <select v-model="location">
          <option value="Lisboa">Lisboa</option>
          <option value="Barreiro">Barreiro</option>
        </select>
        <label>Price: </label>
        <select class="radio" v-model="price">
          <option type="radio" name="cost" value="$"> $</option>
          <option type="radio" name="cost" value="$$"> $$</option>
          <option type="radio" name="cost" value="$$$"> $$$</option>
        </select>
        <label>Zomato Link: </label>
        <input type="text" v-model="zomato_link">

        <label>Obs: </label>
        <input type="text" v-model="obs">
        <div class="action-buttons">
          <button type="submit" class="submitBtn">Submit</button>
          <router-link to="/">Cancel</router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-restaurant',
  data (){
    return{
      restaurant_id: null,
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null
    }
  },
  methods: {
    saveRestaurant(){
      db.collection('restaurants').add({
        name: this.name,
        location: this.location,
        price: this.price,
        zomato_link: this.zomato_link,
        obs: this.obs
      })
      .then(docRef => this.$router.push('/'))
      .catch(error => console.log(err))
    }
  }
}
</script>

<style scoped lang="scss">
#new-restaurant{
  h3{
    text-align: center;
  }
  .form-wrapper{
    .form{
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 300px;
      margin: auto;
      input{
        height: 20px;
      }
      .action-buttons{
        display: flex;
        justify-content: center;
        margin: 10px;
      }
    }
  }
}




</style>



