<template>
  <div id="dashboard">
    <div class="container-restaurant">
      <div class="restaurant--wrap">
          <ul class="restaurant__list" >
            <li class="restaurant__list__unique"  v-for="restaurant in restaurants"  :key="restaurant.id">
                <span class="restaurant__list__unique__text"  v-if="restaurant.location">
                  <p>Cidade: </p> {{restaurant.location}}
                </span>
                <span class="restaurant__list__unique__text" v-if="restaurant.name">
                  <p>Nome: </p> {{restaurant.name}}
                </span>
                <router-link v-bind:to="{name: 'view-restaurant', params: {name: restaurant.name}}">
                  <div class="view-restaurant">
                    View Restaurant
                  </div>
                </router-link>
            </li>
          </ul>
      </div>
      <router-link to="/new">
        <div class="new-restaurant-btn">
          Add New Restaurant
        </div>
      </router-link>
      <button @click="randomRestaurant" >Restaurante Aleatório</button>
        <div class="restaurant__list__random">
          <p>O restaurante aleatório é:</p>
          <span id="restaurant__list__random__result"> </span>
        </div>
    </div>
  </div>

</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data (){
    return{
      restaurants: []
    }
  },
  created(){
    db.collection('restaurants').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        console.log(doc.data());
        const data = {
          'id': doc.id,
          'restaurant_id': doc.data().restaurant_id,
          'name': doc.data().name,
          'location': doc.data().location,
          'obs': doc.data().obs,
          'price': doc.data().price,
          'zomato_link': doc.data().zomato_link
        }
        this.restaurants.push(data)
      })
    })
  },
  methods:{
    randomRestaurant(){
      let random = this.restaurants[Math.floor(Math.random() * this.restaurants.length)]
      document.getElementById('restaurant__list__random__result').innerHTML = random.name + ' <br> ' + random.obs;
    }
  }
}
</script>

<style lang="scss" scoped>
.container-restaurant{
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
      font-size: 35px;
      padding: 50px;
    }
  .restaurant__list{
    display: flex;
    flex-direction: column;
    button{
        align-items: center;
        width: 300px;
        height: 50px;
        border-radius: 5px;
        font-size: 18px;
        margin: auto;
        &:hover{
          color: white;
          background-color: black;
        }
    }
    &__random{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 80px;
      width: 400px;
      margin: auto;
      margin-top: 30px;
      margin-bottom: 30px;
      background: rgb(255, 133, 26);
      display: flex;
      flex-direction: column;

      p{
        font-size: 18px;
      }
      #restaurant__list__random__result{
        font-size: 25px;
        color: black;
      }
  }
    &__unique{
      box-shadow: 3px 5px 62px -13px #848686;
      margin: 20px;
      padding: 30px;
      padding-left: 50px;
      width: 400px;
      background: #f2ffe4;
        &:nth-child(odd){
        box-shadow: none;
      }
      &__text{
        display: flex;
        font-size: 20px;
        align-items: center;
        p{
          font-weight: bold;
          margin-right: 10px;
          font-size: 18px;
        }
      }
    }
  }
}
.view-restaurant{
  width: 200px;
  height: 40px;
  background: lightblue;
  align-items: center;
  display: flex;
  justify-content: center;

}
.new-restaurant-btn{
  width: 200px;
  height: 50px;
  background: lightblue;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 500px) {
.container-restaurant{
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
      font-size: 35px;
      padding: 50px;
    }
  .restaurantList{
    max-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .restaurant{
      box-shadow: 3px 5px 62px -13px #848686;
      width: 300px;
      margin: 20px;
      padding: 50px;
      background: rgb(242, 255, 228);
        &:nth-child(odd){
        -webkit-box-shadow: 3px 5px 62px -13px #848686;
        -moz-box-shadow: 3px 5px 62px -13px #848686;
        box-shadow: 3px 5px 62px -13px #848686;
      }
      .restaurantTextSpan{
        display: flex;
        font-size: 26px;
        p{
          font-size: 22px;
          font-weight: bold;
          margin-right: 10px;
        }
      }
    }
  }
}
}



</style>



