<template>
  <div id="edit-restaurant">
    <h3>Edit Restaurant</h3>
    <div class="form-wrapper">
      <form @submit.prevent="updateRestaurant" class="form">
        <label>Name: </label>
        <input type="text" v-model="name" required>

        <select v-model="location">
          <option value="Lisboa">Lisboa</option>
          <option value="Barreiro">Barreiro</option>
        </select>

        <select class="radio" v-model="price">
          <option type="radio" name="cost" value="$"> $</option>
          <option type="radio" name="cost" value="$$"> $$</option>
          <option type="radio" name="cost" value="$$$"> $$$</option>
        </select>

        <label>Zomato Link: </label>
        <input type="text" v-model="zomato_link">

        <label>Obs: </label>
        <input type="text" v-model="obs">

        <div class="action-buttons">
          <button type="submit" class="submitBtn">Submit</button>
          <router-link to="/">Cancel</router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-restaurant',
  data (){
    return{
      restaurant_id: null,
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null
    }
  },
  beforeRouteEnter(to, from, next){
    db.collection('restaurants').where('name', '==', to.params.name).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.price = doc.data().price
          vm.zomato_link = doc.data().zomato_link
          vm.obs = doc.data().obs
        })
      })
    })
  },
  watch:{
    '$route': 'fetchData'
  },
  methods: {
    fetchData(){
      db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.name = doc.data().name
          this.location = doc.data().location
          this.price = doc.data().price
        })
      })
    },
    updateRestaurant(){
      db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            name: this.name,
            location: this.location,
            price: this.price,
            zomato_link: this.zomato_link,
            obs: this.obs
          })
          .then(() => {
            this.$router.push({
              name: 'view-restaurant', params : {name: this.name}
            })
          })
        })
      })
    }
  }
}
</script>

<style scoped lang="scss">
#edit-restaurant{
  h3{
    text-align: center;
  }
  .form-wrapper{
    .form{
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 300px;
      margin: auto;
      input{
        height: 20px;
      }
      .action-buttons{
        display: flex;
        justify-content: center;
        margin: 10px;
      }
    }
  }
}
</style>



