<template>
  <div id="view-restaurant">
    <ul>
      <li>
        <h4>{{name}}</h4>
        <p>{{zomato_link}}</p>
        <p>{{price}}</p>
        <p>{{location}}</p>
        <p>{{obs}}</p>
      </li>
    </ul>
    <router-link to="/">Back</router-link>
    <router-link v-bind:to="{name: 'edit-restaurant', params: {name:name}}" class="edit-btn">Edit</router-link>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-restaurant',
  data (){
    return{
      restaurant_id: null,
      name: null,
      location: null,
      obs: null,
      price: null,
      zomato_link: null
    }
  },
  beforeRouteEnter(to, from, next){
    db.collection('restaurants').where('name', '==', to.params.name).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.price = doc.data().price
          vm.zomato_link = doc.data().zomato_link
          vm.obs = doc.data().obs
        })
      })
    })
  },
  watch:{
    '$route': 'fetchData'
  },
  methods: {
    fetchData(){
      db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.name = doc.data().name
          this.location = doc.data().location
          this.price = doc.data().price
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



